@import url('https://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css?family=Roboto');

* {
  font-family: Raleway, sans-serif !important;
}
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  min-width: 320px;
  background: #fafafa;
}
#root {
  box-sizing: border-box;
  max-width: 640px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}
header {
  text-align: center;
  margin-bottom: 20px;
}
section {
  text-align: center;
  margin: 20px 0;
}
h1 {
  font-size: 50px;
  font-weight: normal;
  letter-spacing: 5px;
  margin: 0;
}
h2 {
  font-weight: normal;
  margin: 0;
  font-size: 16px;
  font-style: italic;
}
h3 {
  margin: 10px 0;
}
a {
  color: #00bcd4;
}
.key_entry {
  display: inline-block;
  margin: 0 10px 5px 0;
}
.search_form {
  display: inline-block;
  width: 270px;
  text-align: left;
  position: relative;
}
.search_label {
  display: block;
  margin-bottom: 5px;
}
.search_field_container {
  width: 100% !important;
}
.search_field {
  background: #eeeeee;
  height: 40px !important;
  transition: background 0.25s ease;
}
.search_field_focused {
  background: #e0f7fa;
  transition: background 0.25s ease;
}
.search_field:before {
  border-bottom-width: 0 !important;
}
.search_field:after {
  border-bottom-color: #00bcd4 !important;
}
.search_input {
  padding: 10px !important;
}
.swap_button {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin: 0 10px !important;
  padding: 0 !important;
  vertical-align: bottom !important;
}
.search_results_popper {
  width: 100% !important;
  margin-top: 10px !important;
  z-index: 10 !important;
}
.search_results_paper {
  max-height: calc(4 * 40px);
  overflow-y: auto;
  border-radius: 0 !important;
}
.search_results_item {
  height: 40px !important;
  padding: 0 10px !important;
  font-size: 16px !important;
}
.search_results_item_metanode {
  margin-right: 5px;
}
.metanode_chip {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  min-height: 30px;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  font-weight: 900;
  font-size: 14px;
  background: #616161;
  color: #ffffff;
  /* font-family: "Roboto" !important; */
}
.metanode_chip[data-metanode='Gene'] {
  background: #0096ff;
  letter-spacing: 1px;
}
.metanode_chip[data-metanode='Compound'] {
  background: #ff2600;
  letter-spacing: 1px;
}
.metanode_chip[data-metanode='Anatomy'] {
  background: #008f00;
  letter-spacing: 1px;
}
.metanode_chip[data-metanode='Disease'] {
  background: #8e4f00;
}
.metanode_chip[data-metanode='Side Effect'] {
  background: #ffeb3b;
  color: #000000;
}
.metanode_chip[data-metanode='Symptom'] {
  background: #a5abb6;
  letter-spacing: 1px;
}
.metanode_chip[data-metanode='Biological Process'] {
  background: #ff9300;
}
.metanode_chip[data-metanode='Cellular Component'] {
  background: #ff9300;
}
.metanode_chip[data-metanode='Molecular Function'] {
  background: #ff9300;
}
.metanode_chip[data-metanode='Pathway'] {
  background: #ff9300;
}
.metanode_chip[data-metanode='Pharmalogical Class'] {
  background: #ff85ff;
}
.search_results_item_name {
  font-weight: 600;
}
.search_results_item_identifier {
  margin-left: auto;
  font-style: italic;
  color: #757575;
}

@media only screen and (max-width: 640px) {
  .search_form {
    width: 100%;
  }
  .search_form {
    display: block !important;
    margin: 0 auto !important;
  }
  .swap_button {
    display: block !important;
    margin: 10px auto !important;
  }
}
